import {Routes, Route } from 'react-router-dom';

import './App.css';
import { LangState } from './context/lang';
import Header from './Header/Header';
import MainPageSlider from './MainPageSlider/MainPageSlider';
import NewsSection from './NewsSection/NewsSection';
import About from './About/About';
import MainContacts from './MainContacts/MainContacts';
import AllContacts from './AllContacts/AllContacts';
import Sisterhood from './Sisterhood/Sisterhood';
import Rector from './Rector/Rector';
import Parish from './Parish/Parish';
import OrthodoxFaith from './OrthodoxFaith/OrthodoxFaith';
import Footer from './Footer/Footer';
import Schedule from './Schedule/Schedule';
import { Fragment } from 'react';

function App() {
  return (
    <LangState>
      <Header />
      <Routes>
        <Route path='' element={
          <Fragment>
            <MainPageSlider />
            <NewsSection />
            <About />
            <MainContacts />
          </Fragment>
        } />
        <Route path='/parish/*' element={<Parish />} />
        <Route path='/rector' element={<Rector />} />
        <Route path='/sisterhood' element={<Sisterhood />} />
        <Route path='/schedule' element={<Schedule />} />
        <Route path='/orthodox-faith/*' element={<OrthodoxFaith />} />
        <Route path='/contacts' element={<AllContacts />} />
      </Routes>
      <Footer />
    </LangState>
  );
}

export default App;