import { useContext } from 'react';
import { Link } from 'react-router-dom';

import style from './Header.module.css';
import logo from '../assets/images/logo_site.png';

import { commonTranslations } from '../config';
import LangContext from '../context/lang';

const Header = () => {
    const { lang, changeLang } = useContext(LangContext);

    const navElements = [
        {
            href: '/',
            en: 'MAIN',
            ru: 'ГЛАВНАЯ',
        },
        {
            href: '/parish',
            en: commonTranslations['parishUpper']['en'],
            ru: commonTranslations['parishUpper']['ru'],
        },
        {
            href: '/rector',
            en: commonTranslations['rectorUpper']['en'],
            ru: commonTranslations['rectorUpper']['ru'],
        },
        {
            href: '/sisterhood',
            en: commonTranslations['sisterhoodUpper']['en'],
            ru: commonTranslations['sisterhoodUpper']['ru'],
        },
        {
            href: '/schedule',
            en: commonTranslations['scheduleUpper']['en'],
            ru: commonTranslations['scheduleUpper']['ru'],
        },
        {
            href: '/orthodox-faith',
            en: commonTranslations['orthodoxFaithUpper']['en'],
            ru: commonTranslations['orthodoxFaithUpper']['ru'],
        },
        {
            href: '/contacts',
            en: commonTranslations['contactsUpper']['en'],
            ru: commonTranslations['contactsUpper']['ru'],
        },
    ];

    const texts = {
        donate: {
            en: 'DONATE',
            ru: 'ПОЖЕРТВОВАНИЕ',
        }
    }
    const langStyles = {};
    for (const langKey of ['en', 'ru']) {
        langStyles[langKey] = lang === langKey ? style.ActiveLang : style.InactiveLang;
    }

    return (
        <header>
            <div className={style.HeaderTop}>
                <div className={style.HeaderTop__Box}>
                    <Link to='/' className={style.HeaderTop__Brand}>
                        <img src={logo} alt='Holy Trinity Russian Orthodox Church' />
                    </Link>
                    <nav className={style.HeaderTopNav}>
                        <ul className={style.HeaderTopNav__Items}>
                            {navElements.map(element => (
                                <li>
                                    <Link className={style.HeaderTopNav__Item} to={element.href}>{element[lang]}</Link>
                                </li>
                            ))}
                        </ul>
                    </nav>
                </div>
            </div>
            <div className={style.HeaderBottom}>
                <div className={style.HeaderBottom__Box}>
                    <Link to='/parish/support-us' className={style.SupportUs}>{texts['donate'][lang]}</Link>
                    <button className={`${style.Languages} ${langStyles['ru']}`} onClick={() => changeLang('ru')}>RU</button>
                    <button className={`${style.Languages} ${langStyles['en']}`} onClick={() => changeLang('en')}>EN</button>
                </div>
            </div>
        </header>
        )}

export default Header;