import { Fragment, useContext } from 'react';
import { Routes, Route, Link } from 'react-router-dom';

import LangContext from '../context/lang';
import { assetsBaseLocation, commonTranslations } from '../config';

import style from './OrthodoxFaith.module.css';
import Basis from './Basis/Basis';
import SignOfTheCross from './SignOfTheCross/SignOfTheCross';
import Prayer from './Prayer/Prayer';
import DivineService from './DivineService/DivineService';
import Sacraments from './Sacraments/Sacraments';
import OrthodoxTemple from './OrthodoxTemple/OrthodoxTemple';


const OrthodoxFaith = () => {
    const { lang } = useContext(LangContext);
    const assets = `${assetsBaseLocation}/orthodox-faith`;
    const navElements = [
        {
            href: '/orthodox-faith',
            en: commonTranslations['basisUpper']['en'],
            ru: commonTranslations['basisUpper']['ru'],
        },
        {
            href: '/orthodox-faith/sign-of-the-cross',
            en: commonTranslations['sighOfTheCrossUpper']['en'],
            ru: commonTranslations['sighOfTheCrossUpper']['ru'],
        },
        {
            href: '/orthodox-faith/prayer',
            en: commonTranslations['prayerUpper']['en'],
            ru: commonTranslations['prayerUpper']['ru'],
        },
        {
            href: '/orthodox-faith/divine-service',
            en: commonTranslations['divineServiceUpper']['en'],
            ru: commonTranslations['divineServiceUpper']['ru'],
        },
        {
            href: '/orthodox-faith/sacraments',
            en: commonTranslations['sacramentsUpper']['en'],
            ru: commonTranslations['sacramentsUpper']['ru'],
        },
        {
            href: '/orthodox-faith/orthodox-temple',
            en: commonTranslations['orthodoxTempleUpper']['en'],
            ru: commonTranslations['orthodoxTempleUpper']['ru'],
            addStyle: style.Width19,
        },
    ];
    
    return (
        <Fragment>
            <div className={style.ImageWrapper}>
                <img className={style.Image} src={`${assets}/orthodox-faith-top.png`}  alt='' />
            </div>
            <section className={style.MainWrapper}>
                <div className={style.MainBox}>
                    <div className={style.HeaderTopNav__Items}>
                        {navElements.map(element => (
                            <Link className={style.HeaderTopNav__Item + ' ' + element.addStyle} to={element.href}>
                                <div>{element[lang]}</div>
                            </Link>
                        ))}
                    </div>
                </div>
            </section>
            <Routes>
                <Route path='' element={<Basis />} />
                <Route path='sign-of-the-cross' element={<SignOfTheCross />} />
                <Route path='prayer' element={<Prayer />} />
                <Route path='divine-service' element={<DivineService />} />
                <Route path='sacraments' element={<Sacraments />} />
                <Route path='orthodox-temple' element={<OrthodoxTemple />} />
            </Routes>
        </Fragment>
    )
}

export default OrthodoxFaith;