const Schedule = () => {

  const getDays = (year, month) => {
    const startDate = new Date(year, month);
    const humanWeekDay = startDate.getDay() !== 0 ? startDate.getDay() : 7;
    startDate.setDate(startDate.getDate() - humanWeekDay + 1);
    const days = [];
    for (let i = 0; i < 42; i += 1) {
      days.push(new Date(startDate));
      startDate.setDate(startDate.getDate() + 1);
    }
    return days;
  };

  const { lang } = useContext(LangContext);
  const assets = `${assetsBaseLocation}/schedule`;
  const [yearMonthState, setYearMonthState] = useState({
    year: (new Date()).getFullYear(),
    month: (new Date()).getMonth(),
  });
  const [scheduleMonthsState, setScheduleMonthsState] = useState({});
  const texts = {
    mainTitle: {
      en: 'SCHEDULE',
      ru: 'РАСПИСАНИЕ',
    },
    title: {
      en: 'Dear friends this section offers a calendar of services and other parish events',
      ru: 'Дорогие друзья, в этом разделе вам предлагается календарь богослужений и других приходских событий',
    },
  };
  const calendarDays = getDays(yearMonthState.year, yearMonthState.month);

  useEffect(() => {
    const curMonth = new Date(yearMonthState.year, yearMonthState.month);
    let prevMonth = new Date(curMonth);
    prevMonth.setMonth(prevMonth.getMonth() - 1);
    let nextMonth = new Date(curMonth);
    nextMonth.setMonth(nextMonth.getMonth() + 1);
    const yearsMonthsToQuery = [
      prevMonth, curMonth, nextMonth
    ];
    // to store new months schedules to add
    const monthsToAdd = {};
    // iterate over requied months schedules
    for (const month of yearsMonthsToQuery) {
      const yearMonthKey = `${month.getFullYear()}-${month.getMonth()}`;
      // if required month schedule not in memory - pull it
      if (!(yearMonthKey in scheduleMonthsState)) {
        // if required month schedule available - fill it
        fetch(`${assetsBaseLocation}/schedule/data/${yearMonthKey}.json`)
        .then(response => response.json())
        .then(data => {
          monthsToAdd[yearMonthKey] = data;
          setScheduleMonthsState((prevState) => ({...prevState, ...monthsToAdd}));
        })
        // if not - fill with empty schedule
        .catch(() => {
          monthsToAdd[yearMonthKey] = {};
          setScheduleMonthsState((prevState) => ({...prevState, ...monthsToAdd}));
        });
      }
    }
    
  }, [yearMonthState.month])

  const dayRender = (day) => {
    const yearMonthKey = `${day.getFullYear()}-${day.getMonth()}`;
    if ((yearMonthKey in scheduleMonthsState)
      && (day.getDate() in scheduleMonthsState[yearMonthKey])) {
      const monthDaysData = scheduleMonthsState[yearMonthKey][day.getDate()];
      return (
        monthDaysData.map(dayData => <div className={style.DayTextsContainer}>
          {dayData['title'] ? <div className={style.DayTitle}>{dayData['title'][lang]}</div> : null}
          {dayData['text'] ? <div className={style.DayText}>{dayData['text'][lang]}</div> : null}
        </div>)
      )
    } else {
      return null;
    }
  }

  return (
    <>
      <div className={style.ImageWrapper}>
        <img className={style.Image} src={`${assets}/schedule-top.png`} alt="" />
      </div>
      <section className={style.MainWrapper}>
        <div className={style.MainBox}>
          <div className={style.PathBox}>
            <Link to="/" className={style.PathLink}>{commonTranslations.main[lang]}</Link>
            <div className={style.PathDelimiter}>/</div>
            <div className={style.ActivePathPage}>{commonTranslations.schedule[lang]}</div>
          </div>
          <div className={style.MainTitle}>{texts.mainTitle[lang]}</div>
          <div className={style.MainColumns_Box}>
            <div className={style.ParagraphTitle}>{texts.title[lang]}</div>
            <div className={style.ControlsContainer}>
              <div
                className={style.ControlArrows}
                onClick={() => {
                  setYearMonthState((prevState) => {
                    const month = prevState.month === 0 ? 11 : prevState.month - 1;
                    const year = month === 11 ? prevState.year - 1 : prevState.year;
                    return { year, month };
                  });
                }}
              >
                &larr;
              </div>
              <div className={style.ControlsMonth}>
                {`${(new Date(yearMonthState.year, yearMonthState.month)).toLocaleString(lang, {month: 'long'})} ${yearMonthState.year}`}
              </div>
              <div
                className={style.ControlArrows}
                onClick={() => {
                  setYearMonthState((prevState) => {
                    const month = prevState.month === 11 ? 0 : prevState.month + 1;
                    const year = month === 0 ? prevState.year + 1 : prevState.year;
                    return { year, month };
                  });
                }}
              >
                &rarr;
              </div>
            </div>
            <div className={style.CalendarContainer}>
              {calendarDays.map(
                (day) => {
                  return (
                    <div className={`${style.DayContainer}${day.getMonth() === yearMonthState.month ? '' : ` ${style.DayCurrentMonth}`}`}>
                      <div className={style.DayNumber}>
                        {day.getDate()}
                      </div>
                      {dayRender(day)}
                    </div>
                  )
                }
              )}
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Schedule;
