import { Fragment, useContext } from 'react';
import { Routes, Route, Link } from 'react-router-dom';

import LangContext from '../context/lang';
import { assetsBaseLocation, commonTranslations } from '../config';

import style from './Parish.module.css';
import SupportUs from './SupportUs/SupportUs';
import ParishHistory from './ParishHistory/ParishHistory';
import OurTemple from './OurTemple/OurTemple';
import TrinityHerald from './TrinityHerald/TrinityHerald';
import ParishToday from './ParishToday/ParishToday';


const Parish = () => {
    const { lang } = useContext(LangContext);
    const assets = `${assetsBaseLocation}/parish`;
    const navElements = [
        {
            href: '/parish',
            en: commonTranslations['parishHistoryUpper']['en'],
            ru: commonTranslations['parishHistoryUpper']['ru'],
        },
        {
            href: '/parish/our-temple',
            en: commonTranslations['ourTempleUpper']['en'],
            ru: commonTranslations['ourTempleUpper']['ru'],
        },
        {
            href: '/parish/parish-today',
            en: commonTranslations['parishTodayUpper']['en'],
            ru: commonTranslations['parishTodayUpper']['ru'],
        },
        {
            href: '/parish/trinity-herald',
            en: commonTranslations['trinityHeraldUpper']['en'],
            ru: commonTranslations['trinityHeraldUpper']['ru'],
        },
        {
            href: '/parish/support-us',
            en: commonTranslations['supportUsUpper']['en'],
            ru: commonTranslations['supportUsUpper']['ru'],
        }
    ];

    return (
        <Fragment>
            <div className={style.ImageWrapper}>
                <img className={style.Image} src={`${assets}/parish-top.png`} alt='' />
            </div>
            <section className={style.MainWrapper}>
                <div className={style.MainBox}>
                    <div className={style.HeaderTopNav__Items}>
                        {navElements.map(element => (
                            <Link className={style.HeaderTopNav__Item} to={element.href}>
                                <div>{element[lang]}</div>
                            </Link>
                        ))}
                    </div>
                </div>
            </section>
            <Routes>
                <Route path='' element={<ParishHistory />} />
                <Route path='our-temple' element={<OurTemple />} />
                <Route path='parish-today' element={<ParishToday />} exact />
                <Route path='trinity-herald' element={<TrinityHerald />} exact />
                <Route path='support-us' element={<SupportUs />} exact />
            </Routes>
        </Fragment>
    )
}

export default Parish;